var React = require('react');
var WeekDayItem = require('./WeekDayItem');
var utils = require('../utils/helpers');
var convertTempF = utils.convertTempF;

class Detail extends React.Component {
    render() {
        var props = this.props.location.state;
        return (
            <div>
                <WeekDayItem day={props} />
                <div className='description-container'>
                    <p>{props.city}</p>
                    <p>{props.weather[0].description}</p>
                    <p>min temp: {convertTempF(props.temp.min)} degrees</p>
                    <p>max temp: {convertTempF(props.temp.max)} degrees</p>
                    <p>humidity: {props.humidity}</p>
                </div>
            </div>
        )
    }
}

module.exports = Detail;


