import React, { Component } from 'react';
import queryString from 'query-string';
import api from '../utils/api';
import DayItem from './DayItem';
import WeekDayItem from './WeekDayItem';

class Forecast extends Component {
    constructor(props) {
        super(props);
        this.state = {
            forecastDataToday: [],
            forecastDataUpcoming: [],
            loading: true
        }
    }
    componentDidMount() {
        this.city = queryString.parse(this.props.location.search).city;
        this.makeRequest(this.city);
    }
    componentWillReceiveProps(nextProps) {
        this.city = queryString.parse(nextProps.location.search).city;
        this.makeRequest(this.city);
    }
    makeRequest(city) {
        this.setState(function () {
            return {
                loading: true
            }
        })

        api.getWeather(city)
            .then(function (res) {
                this.setState(function () {
                    return {
                        loading: false,
                        forecastDataToday: res[0].data,
                        forecastDataUpcoming: res[1].data,
                    }
                })
            }.bind(this))
    }
    handleClick(city) {
        city.city = this.city;
        this.props.history.push({
            pathname: '/details/' + this.city,
            state: city,
        })
    }
    render() {
        return this.state.loading === true
            ? <h1 className='forecast-header'> Loading </h1>
            : <div>
                <h1 className='forecast-header'>{this.city}</h1>
                <DayItem day={this.state.forecastDataToday} ></DayItem>
                
                { this.state.forecastDataUpcoming.list.map((day, index) => {
                    return <WeekDayItem onClick={this.handleClick.bind(this, day)} day={day} key={index}></WeekDayItem>
                }) }

            </div>
    }
}

export default Forecast;



