var moment = require('moment');

function convertTempF(kelvin) {
    return parseInt(((kelvin - 273.15) * 1.8000 + 32.00), 10)
}
function convertTempC(kelvin) {
    return parseInt(((kelvin - 273.15)), 10)
}

function getDate(unixTimestmap) {
    return moment.unix(unixTimestmap).format('DD');
}
function getFullDate(unixTimestmap) {
    return moment.unix(unixTimestmap).format('LL');
}

module.exports = {
    convertTempF: convertTempF,
    convertTempC: convertTempC,
    getDate: getDate,
    getFullDate: getFullDate
}


