var React = require('react');
var utils = require('../utils/helpers');
var getFullDate = utils.getFullDate;
var convertTempF = utils.convertTempF;
var convertTempC = utils.convertTempC;

function DayItem(props) {
    var date = getFullDate(props.day.dt);
    var icon = props.day.weather[0].icon;
    var tempF = convertTempF(props.day.main.temp);
    var tempC = convertTempC(props.day.main.temp);
    return (
        <div onClick={props.onClick} className='dayContainer'>
            <img className='weather' src={require('../images/weather-icons/' + icon + '.svg')} alt='Weather' />
            <h2 className='subheader'>{date}</h2>

            <h2 className='subheader'>{tempF}&#176;F</h2>
            <h2 className='subheader'>{tempC}&#176;C</h2>
        </div>
    )
}

module.exports = DayItem;


